import Vue from 'vue'
import VueRouter from 'vue-router'
// import Search from './../components/search/search'
import mymusic from './../components/mymusic/myMusic'
import findmusic from './../components/findMusic/findmusic'
import community from './../components/community/Community'
import musicrecommend from './../components/musicrecommend/musicrecommend'
//import Findsheet from './../components/findsheet/findsheet'
import setColor from './../components/setColor'
import info from './../components/info'
import editname from './../components/editname'



Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      // +++++++++++++++++++++ 
      path: '/',
      redirect: '/mymusic'
    },
    {
      //    
      path: '/mymusic',
      component: mymusic
    },
    {
      // +++++++++++++++++++++ findmusic 
      path: '/findmusic',
      component: findmusic,
        //router
      children: [{
        // 
        path: '',
        redirect: '/findmusic/musicrecommend'
      },
      {
        // findmusic   
        path: '/findmusic/musicrecommend',
        component: musicrecommend
      }
          //,
      //{
        // findmusic   
        //path: '/findmusic/findsheet',
        //component: Findsheet
      //}
  ]
    },
    {
      // +++++++++++++++++++++ 
      path: '/community',
      component: community
    },
    {
        path: '/info',
        component: info
    },
    {
        path: '/editname',
        component: editname
    },
    {
      // +++++++++++++++++++++ 
      path: '/setColor',
      component: setColor
    }
    // {
    //   // 
    //   path: '/search',
    //   name: 'Search',
    //   component: Search
    // }
  ]
})



